
#app {
  font-family: "Helvetica Neue",
    Arial,
    "Hiragino Kaku Gothic ProN",
    "Hiragino Sans",
    Meiryo,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

header{
    width: 100%;
    height: 64px;
    background: olive;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.logo{
    line-height: 15px;
    margin: 0 24px;
    font-size: 18px;
}
.logo h1 a{
    color: #fff;
}
.menu{
    list-style: none;
    margin: 0;
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 18px;
}
a{
    display: block;  
    text-decoration: none;
}
.btn{
    width: 120px;
    line-height: 64px;
    text-align: center;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.btn-text{
    width: 100%;
    height: 100%;
    color: #fff;
}
.btn-text:hover{
    background: rgb(211, 211, 145);
    color: olive;
    -webkit-transition: .2s;
    transition: .2s;
}


footer{
    font-size: 12px;
    color: #777;
    text-align: center;
    height: 20px;
    line-height: 20px;
}

.textbox{
    color: #333;
    text-align: center;
    padding: 32px;
    width: 800px;
    margin: 8px auto;
}
.textbox h1{
    font-size: 48px;
}
.textbox p{
    font-size: 18px;
    line-height: 24px;
}























.content{
    color: #333;
    text-align: center;
    padding: 20px;
    width: 800px;
    margin: 10px auto;
}

.about section{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.profile {
    margin: 10px auto;
}
.info .name{
    margin: 0;
    padding: 10px;
}
img{
    width: 120px;
    height: 120px;
    padding: 12px;
}
.history{
   width: 350px;
   /* padding: 18px; */
   margin: 10px auto;
}
.history h2{
    font-weight: normal;
    margin: 0;
}

.contact{
    text-align: center;
}
.contact section{
    margin-bottom: 32px;
}
.contact section p:nth-child(2){
    font-size: 14px;
}
#caution{
    color: #c92424;
}

input,
button,
textarea,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: inherit;
}
.form {
    text-align: left;
    width: 500px;
    margin: 10px auto;
    padding: 24px 12px;
    border: solid 1px #bbb;
}
.form div{
    padding: 12px;
}
.form p{
    font-size: 12px;
    color: #eb4343;
    margin: 5px 0;
}
label{
    margin-right: 12px;
}
input{
    border: 1px solid #bbb;
    outline: 0;
    height: 32px;
    border-radius: 4px;
}
input:focus {
    border: 1px solid#2d9fbb;
    -webkit-box-shadow: inset 0 0 4px #2d9fbb;
    box-shadow: inner 0 0 4px #2d9fbb;
}
select{
    border: 1px solid #bbb;
    outline: 0;
    height: 32px;
    cursor: pointer;
    padding: 5px 15px 5px 5px;
    width: 150px;
    height: 40px;
    border-radius: 4px;
}
select:focus{
    border: 1px solid#2d9fbb;
    -webkit-box-shadow: inset 0 0 4px #2d9fbb;
    box-shadow: inner 0 0 4px #2d9fbb;
}
textarea{
    border: 1px solid #bbb;
    outline: 0;
    height: 80px;
    width: 476px;
    resize: none;
    border-radius: 4px;
}
textarea:focus{
    border: 1px solid#2d9fbb;
    -webkit-box-shadow: inset 0 0 4px #2d9fbb;
    box-shadow: inner 0 0 4px #2d9fbb;
}
button{
    display: block;
    background: #2d9fbb;
    border: none;
    padding: 5px;
    width: 80px;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
    margin: 0 auto;
    -webkit-box-shadow: 0 3px 0 #1d697c;
            box-shadow: 0 3px 0 #1d697c;
}
button:hover{
    background: #d9e9f7;
    color:  #2d9fbb;
    -webkit-box-shadow: 0 3px 0 #2d9fbb;
            box-shadow: 0 3px 0 #2d9fbb;
}
button:focus{
    outline: 0;
}
button:active{
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}

.worksText{
    text-align: center;
    font-size: 16px;
}
.workContainer{
    width: 800px;
    margin: 10px auto;
    margin-bottom: 50px;
}
.workLink{
    background: #adc978;
    color: #222;
    width: 400px;
    border-radius: 10px;
    border: 2px solid #adc978;
    margin: 20px auto;
}
.workLink:hover{
    opacity: .6;
}
.workLink h3,
.workLink p{
    padding: 10px;
}
.workLink h3{
    margin: 0;
}
.workLink p{
    background: #fff;
    /* height: 100px; */
    margin: 0 auto;
    border-radius: 0 0 10px 10px;
    font-size: 14px;
}


.studyContainer{
    width: 800px;
    margin: 0 auto;
}
.studyContainer section{
    border: 1px solid #96aa98;
    background: #f3f7f1;
}
.sectionMenu{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.sectionTitle{
    margin: 0;
    border: 1px solid #96aa98;
    border-bottom: none;
    width: 50px;
    padding: 10px;
    border-radius: 10px 10px 0 0;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.sectionTitle.isShow{
    background: #f3f7f1;
}

.textArea{
    border-bottom: 1px dashed #96aa98;
    padding: 10px;
    margin: 10px;
}
.textArea h3,
.textArea p{
    margin: 10px;
}

.textArea a{
    font-weight: bold;
    margin: 10px;
    font-size: 17px;
    color: #3c6d9e;
}
.textArea a:hover{
    color: #80addb;
}
















